.gallery {
  flex-wrap: wrap;
  gap: 15px;
  display: flex;
}

.photo-card {
  width: 300px;
  height: 350px;
  outline: 1px solid red;
}

.image-container {
  width: inherit;
  height: 150px;
  outline: 1px solid #00f;
}

.preview-image {
  width: 100%;
  height: 100%;
  object-fit: cover;
}

.button-container {
  justify-content: center;
  align-items: center;
  display: flex;
}

.load-more {
  text-align: center;
  text-transform: uppercase;
  color: #fff;
  cursor: pointer;
  background-color: #007bff;
  border: none;
  border-radius: 5px;
  padding: 10px 20px;
  font-size: 1.2em;
  font-weight: bold;
  transition: background-color .3s ease-in-out, transform .2s ease-in-out;
  display: inline-block;
}

.movie-list {
  flex-wrap: wrap;
  justify-content: center;
  margin: 0;
  padding: 0;
  list-style: none;
  display: flex;
}

.load-more:hover {
  background-color: #0056b3;
  transform: scale(1.05);
}

.movie-card {
  width: 300px;
  background-color: #fff;
  border-radius: 10px;
  margin: 20px;
  transition: transform .3s ease-in-out, box-shadow .3s ease-in-out;
  position: relative;
  overflow: hidden;
  box-shadow: 0 4px 8px rgba(0, 0, 0, .2);
}

.movie-card:hover {
  transform: translateY(-5px);
  box-shadow: 0 6px 12px rgba(0, 0, 0, .3);
}

.movie-card img {
  width: 100%;
  height: auto;
  border-top-left-radius: 10px;
  border-top-right-radius: 10px;
}

.movie-info {
  padding: 15px;
}

.movie-info h2 {
  color: #333;
  margin: 0;
  font-size: 1.8em;
}

.movie-info p {
  color: #777;
  margin: 5px 0;
  font-size: 1.2em;
}

.loader-container {
  width: 100%;
  height: 100%;
  opacity: 1;
  visibility: visible;
  transition: opacity var(--primary-transition), visibility var(--primary-transition);
  background-color: #5897fb;
  position: fixed;
  top: 0;
  left: 0;
}

.loader {
  width: 180px;
  height: 180px;
  justify-content: center;
  align-items: center;
  margin-top: -90px;
  margin-left: -90px;
  display: flex;
  position: absolute;
  top: 50%;
  left: 50%;
}

.loader:before, .loader:after {
  content: "";
  filter: drop-shadow(0 0 1rem rgba(255, 255, 255, .75));
  border-radius: 50%;
  animation: pulsOut 1.8s ease-in-out infinite;
  position: absolute;
}

.loader:before {
  width: 100%;
  padding-bottom: 100%;
  animation-name: pulsIn;
  box-shadow: inset 0 0 0 1rem #fff;
}

.loader:after {
  width: calc(100% - 2rem);
  padding-bottom: calc(100% - 2rem);
  box-shadow: 0 0 #fff;
}

@keyframes pulsIn {
  0% {
    opacity: 1;
    box-shadow: inset 0 0 0 1rem #fff;
  }

  50%, 100% {
    opacity: 0;
    box-shadow: inset 0 0 #fff;
  }
}

@keyframes pulsOut {
  0%, 50% {
    opacity: 0;
    box-shadow: 0 0 #fff;
  }

  100% {
    opacity: 1;
    box-shadow: 0 0 0 1rem #fff;
  }
}

.is-hidden {
  display: none;
}

.option-container {
  outline: 1px solid red;
}

